.modal-container, .overlay {
    background-color: rgba(0, 0, 0, 0.6);
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    position: fixed;
    z-index: 100;
    
}


.edit-profile__container {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: .5em .2em;
    background-color: white;
    z-index: 100;
    border-radius: 10px;
}

.edit__banner {
    display: flex;
    align-items: center;
}

.close-modal__container {
    border-radius: 50%;
    
    padding: .1em;
}

.close-modal {
    margin-right: 2.3em;
    font-size: 1.5rem;
    font-weight: 600;
    position: relative;
    top: -5px;
    margin-left: .5em;
    color: black;
    border-radius: 50%;
    transition: all .2s;
    padding: 10px;
}



.close-modal:hover {
    background-color: lightgrey;
}

.banner__text {
    font-weight: 700;
    font-size: 1.3rem
}

.save-btn {
    position: relative;
    left: 50%;
    border-radius: 20px;
    border: none;
    background-color: rgba(29, 155, 240, .7);
    padding: .4em 1em;
    top: -5px;
}

.profile-forms {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
}

.form-container {
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 5px;
    margin: 2em 1em;
    padding: .4em;
    min-height: 1rem;
}

label {
    color:rgba(0, 0, 0, .65)
}

.edit:focus-visible {
    outline: none;
}

.edit {
    width: 96%;
    border: none;
    padding: 0;
    text-decoration: none;
}

.username, .location {
    resize: none;
    height: 70%;
}

.small-form {
    height: auto;
}

