.navbar__container__outer {
    left: -14.8rem;
    position: relative;
    margin-left: 14em;
    max-width: 650px;
    margin-right: auto;
    height: auto;
    overflow: auto;
}

.navbar__container__inner {
    position: fixed;
}

.navbar__header {
    padding: 0 3em;
    position: sticky;
}


.navbar__list {
    list-style: none;
    
}

.bold {
    font-weight: 600;
}

.twitter-icon {
    padding: .5em;
    font-size: 2em;
    color: rgb(29, 155, 240);;
}

.twitter-icon:hover {
    background-color: rgba(29, 155, 240, .2);
    border-radius: 2em;
}


.navbar__list-item {
    cursor: pointer;
    text-decoration: none;
    padding: .75em .5em;
    text-align: center;
    justify-content: center;
    font-size: 1.2em;
    color: black;
    display: grid;
    grid-template-columns: 30% 70%;
    margin: .5em;
    transition: all 300ms ease-in;
}

.list-item__content {
    text-align: left;
}

.navbar__list-item:hover {
    color: black;
    border-radius: 2em;
    background-color: hsl(240, 0%, 90%);
}

.navbar__link {
    text-decoration: none;
    color: black;
}

