.profile__header {
    width: 37rem !important;
    justify-content: center;
    position: fixed; 
    height: 3em;
    margin-bottom: 3em;
    font-weight: bold;
    font-size: 1.2em;
    background: rgba(255, 255, 255, .8);
    backdrop-filter: blur(5px);
    z-index: 99;
    border-left: 1px solid rgba(0, 0, 0, 0.075);
    border-right: 1px solid rgba(0, 0, 0, 0.075);
}

.profile__content {
    padding-top: .5em;
    padding-bottom: .5em;
    padding-left: .8em;
    filter: none;
}

.profile__body {
    margin-top: 3em;
}



.profile {

    width: 37rem;
    margin-right: 0;
}